.p-greeting__img {
  background-color: #ececec;
  border-radius: 20px;
  margin-top: 38px;
  padding-top: calc((446/335)*100%);
  position: relative;
}

.p-greeting__img--sp {
  display: block;
  @include mq("tab") {
    display: none;
  }
}

.p-greeting__img img {
  border-radius: 20px;
  height: 100%;
  left: 0;
  object-fit: cover;
  position: absolute;
  top: 0;
  width: 100%;
}

.p-greeting__img-pc {
  background-color: #ececec;
  border-radius: 20px;
  display: none;
  @include mq("tab") {
    align-self: flex-start;
    display: block;
    margin-left: auto;
    margin-top: 7px;
    padding-top: calc((613/1000)*100%);
    width: calc((460/1000)*100%);
  }
  margin-top: 40px;
  padding-top: calc((446/335)*100%);
  position: relative;
  width: 100%;
}

.p-greeting__img-pc img {
  border-radius: 20px;
  height: 100%;
  left: 0;
  object-fit: cover;
  position: absolute;
  top: 0;
  width: 100%;
}
